.load-more-news {
  display: block;
  width: 1120px;
  height: 45px;
  .margin-x(auto);

  color: #2b3033;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  letter-spacing: 0.4px;
  border: 1px solid #cfcfcf;
  background-color: #fff;
}

.telegram-link {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1120px;
  height: 135px;
  margin: 0 auto 20px;

  color: #ffffff;
  font-family: @font-main;
  font-size: 22px;
  line-height: 1.6;
  letter-spacing: -0.9px;
  text-align: center;

  background-color: #3da2f9;
  background-image: url(../img/tg-bg.png);
  background-position-x: 125%;
  background-repeat: no-repeat;

  &:hover {
    color: #fff;
  }
}

.site-footer {
  max-width: 1120px;
  .margin-x(auto);
  padding: 20px 30px;

  color: #f1f1f1;
  font-family: @font-main;
  background-color: #3f4549;

  li {
    line-height: 2;
  }
}

.ndelo-copy {
  margin-top: 15px;

  font-size: 17px;
  line-height: 1.4;
  letter-spacing: -0.3px;
  color: #f1f1f1;
}

.ndelo-email {
  color: @active-color;
  &:hover {
    color: #f1f1f1;
  }
}
