.article-wrap {
  max-width: 1140px;
  .margin-x(auto);

  .theme-block {
    height: auto;
    margin-bottom: 20px;
  }

  .theme-block--that {
    position: sticky;
    top: 20px;
  }
}

.article-content {
  margin-bottom: 20px;
  padding: 30px 10px;

  border: 1px solid #dfdfdf;
  border-bottom: 2px solid @active-color;
  background-color: #fff;
}

.article__title {
  max-width: 85%;
  margin-bottom: 10px;

  color: @active-color-font;
  font-family: @font-main;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.09;
  letter-spacing: -0.4px;
}

.article__short-text {
  margin-bottom: 20px;

  color: #2b3033;
  font-family: @font-main;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.22;
  letter-spacing: -0.4px;
}

.article__meta {
  color: #6d6d6d;
  font-size: 10px;
  letter-spacing: .3px;
}

.article__date,
.article__author {
  font-size: 14px;
  vertical-align: text-top;
}

.article__tags {
  margin-top: 20px;
  .padding-y(12px);
  font-size: 13px;

  border-top: 1px solid #e3e3e3;

  a + a {
    margin-left: 10px;
  }
}

.article__image {
  width: calc(~'100% + 22px');
  margin-bottom: 25px;
  margin-left: -11px;
}

.article__wysiwyg {
  h2, h3, h4, h5, h6 {
    .margin-y(25px, 20px);

    font-size: 18px;
    font-weight: 600;
  }
  p {
    margin-bottom: 17px;

    color: #2b3033;
    font-size: 17px;
    line-height: 1.18;
    letter-spacing: -.3px;
  }
  a {
    color: @active-color-font;
  }
  blockquote {
    position: relative;
    margin: 30px 120px;
    .padding-y(15px);

    line-height: 1.38;

    border-top: 1px solid #3d4348;
    border-bottom: 1px solid #3d4348;

    &::before,
    &::after {
      position: absolute;

      font-family: Georgia, serif;
      font-size: 47px;
      font-style: italic;
      line-height: .5;
      color: #3d4348;
    }

    &::before {
      content: '“';
      top: -5px;
      right: calc(~'100% + 15px');
    }

    &::after {
      content: '“';
      bottom: -5px;
      left: calc(~'100% + 15px');
      transform: rotate(180deg);
    }
  }
}

.article__quiz {
  max-width: 610px;
  margin: 40px auto;
  padding: 25px;

  border: solid 1px #bbbbbb;
}

.article__quiz__title {
  margin-bottom: 30px;

  font-size: 18px;
  font-family: @font-main;
  font-weight: 600;
  letter-spacing: -0.4px;
  color: #2b3033;
}

/**
 * comments
 * start
 */

.article__comments__count {
  display: block;
  margin-bottom: 30px;
  padding-bottom: 15px;

  color: #2b3033;
  font-family: @font-main;
  font-size: 18px;
  font-weight: 600;

  border-bottom: 1px solid #bbbbbb;
}

.article__comments {
  margin-bottom: 50px;
}

.article__comments__item {
  display: flex;

  & + & {
    margin-top: 35px;
  }
}

.article__comments__avatar {
  height: 70px;
  margin-right: 20px;
}

.article__comments__author {
  font-family: @font-main;
  font-weight: 600;
  color: #2b3033;
}

.article__comments__date {
  margin-left: 5px;

  color: #6d6d6d;
  font-size: 14px;
  letter-spacing: 0.3px;
}

.article__comments__text {
  margin-top: 10px;

  color: #3d4348;
  line-height: 1.25;
}

.article__comments__rules {
  display: block;
  margin-bottom: 15px;
  margin-left: auto;

  font-size: 15px;
  font-weight: 600;

  border: none;
  background-color: transparent;
}

.article__comments__form-wrap {
  display: flex;
  align-items: flex-start;
}

.article__comments__form {
  width: 100%;
}

.article__comments__form__avatar {
  margin-right: 20px;
}

.article__comments__form__btn {
  display: block;
  width: 167px;
  margin-top: 20px;
  margin-left: auto;

  color: #fff;
  font-size: 15px;
  font-weight: 500;
  line-height: 24px;
  border: none;
  background-color: #009bf3;
}

/**
 * comments
 * end
 */

/**
 * similar
 * start
 */

.similar {
  max-width: 1120px;
  margin: 0 auto 20px;
  padding: 20px;

  color: #2b3033;
  font-size: 18px;
  font-weight: 600;
  font-family: @font-main;
  letter-spacing: -0.4px;

  background-color: #fff;
  border: #dfdfdf solid 1px;
  border-bottom: 2px solid @active-color;
}

.similar__title {
  margin-bottom: 15px;
}

.similar__item {
  display: block;
  height: 100%;
  padding: 0 9px 10px;

  background-color: #ffffff;
  border: #dfdfdf solid 1px;
}

.similar__item__image {
  width: calc(~'100% + 20px');
  margin-left: -10px;
}

.similar__item__title {
  margin-top: 15px;
}

/**
 * similar
 * end
 */

.report-btn {
  display: block;
  margin-bottom: -20px;
  margin-left: auto;

  color: #2b3033;
  font-size: 15px;
  font-weight: 600;

  border: none;
  background-color: transparent;
}
