.site-header-wrap {
  padding-top: 15px;
  padding-bottom: 20px;

  background-color: #fff;
}

.site-header {
  .container();
}

.top-line {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.hot-news {
  width: 1015px;
  margin-right: 30px;

  color: #fff;
  text-align: center;
  font-family: @font-main;
  font-size: 16px;
  line-height: 26px;
  font-weight: 700;
  border: 1px solid transparent;
  background-color: @active-color;

  transition: all .1s;

  &:hover {
    border-color: @active-color;
    background-color: #fff;
  }
}

/**
 * форма авторизации
 * старт
 */

.user-wrap {
  position: relative;

  &:hover {
    .auth-form-wrap[data-header-form] {
      display: flex;
    }
    .user__logout {
      display: block;
    }
  }
}

.auth-form-wrap {
  display: none;

  position: absolute;
  top: 100%;
  right: 0;
  z-index: 1;

  flex-direction: column;
  align-items: center;
  width: 300px;
  padding: 25px 20px 10px;

  border: 1px solid #dfdfdf;
  border-bottom: 2px solid @active-color;
  background-color: #fff;
}

.auth-form-wrap--sign-in {
  .auth__form {
    border-bottom: 1px solid #e1e0e0;
  }

  .input + .input {
    margin-top: 20px;
  }

  .auth-form__submit-btn {
    width: 158px;
  }
}

.auth-form-wrap--sign-up {
  .input {
    margin-bottom: 20px;
  }
}

.auth-form__title {
  margin-bottom: 10px;

  color: #2b3033;
  font-family: @font-main;
  font-size: 20px;
  font-weight: 700;
}

.auth-form__change-form-btn {
  margin-bottom: 20px;

  color: #3d4348;
  font-size: 14px;
  text-decoration: underline;

  border: none;
  background-color: transparent;
}

.auth__form {
  padding-bottom: 30px;
  label {
    display: flex;
    align-items: center;
    .margin-y(15px, 25px);
  }
}

.auth-form__submit-btn {
  display: block;
  height: 37px;
  .margin-x(auto);

  color: #2b3033;
  background-color: #fff;
  border: 1px solid @active-color;
}

.auth-form__or {
  position: relative;
  top: -12px;
  .padding-x(15px);

  font-family: @font-main;
  font-weight: 600;

  background-color: #fff;
}

.user__username {
  display: flex;
  align-items: center;

  img {
    margin-left: 10px;
  }
}

.user__logout {
  display: none;

  position: absolute;
  top: 100%;
  right: 0;
  padding: 20px;

  border: 1px solid #dfdfdf;
  border-bottom: 2px solid @active-color;
  background-color: #fff;

  a {
    display: block;
    padding: 6px 20px;

    color: #2b3033;
    font-size: 14px;

    border: 1px solid @active-color;
    background-color: #fff;
  }
}

/**
 * форма авторизации
 * конец
 */

.that-line {
  display: flex;
  align-items: center;
}

.logo {
  text-align: center;

  span {
    color: #000;
    font-family: aGildiaExp, serif;
    font-size: 55px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  small {
    font-family: @font-main;
    font-size: 17px;
    font-weight: 700;
    color: #252728;
  }
}

.that-header {
  max-width: 835px;
  max-height: 97px;
}

.header-nav {
  margin-bottom: 20px;

  background-color: #2a2c2c;

  ul {
    .container();
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
  }

  a {
    color: #fff;
    font-size: 17px;
    line-height: 58px;
    letter-spacing: 0.7px;

    &:hover,
    &.current {
      color: @active-color-font;
    }
  }
}
