.wrap {
  .container();
}

a {
  &:hover {
    color: @active-color-font;
  }
}

.theme-block {
  height: 100%;
  .padding-x(10px);

  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-bottom: 2px solid @active-color;
}

.theme-block__title {
  position: relative;

  color: #2b3033;
  text-align: center;
  font-family: @font-main;
  font-size: 14px;
  font-weight: 700;
  line-height: 37px;
  letter-spacing: -0.3px;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;

    display: block;
    width: 100%;
    height: 1px;
    background-color: #dfdfdf;
  }
}

.theme-block--news-feed {
  display: flex;
  flex-direction: column;

  .theme-block__item {
    position: relative;
    .padding-y(20px);

    &:not(:first-of-type)::before {
      content: '';
      position: absolute;
      top: 0;

      display: block;
      width: 100%;
      height: 1px;
      background-color: #dfdfdf;
    }
  }
  .theme-block__item__date {
    font-size: 14px;
    color: #6d6d6d;
  }
  .theme-block__item__title {
    margin-top: 10px;

    line-height: 1.19;
    color: #475056;
  }
  .theme-block__footer {
    position: relative;
    margin-top: auto;
    .padding-y(8px);

    font-size: 15px;
    text-align: center;
    letter-spacing: .3px;

    &::before {
      content: '';
      position: absolute;
      top: 0;

      display: block;
      width: 100%;
      height: 1px;
      background-color: #dfdfdf;
    }
  }
}

.theme-block--concerns {
  .theme-block__item {
    position: relative;
    .padding-y(20px);

    &::after {
      content: '';
      position: absolute;
      bottom: 0;

      display: block;
      width: 100%;
      height: 1px;
      background-color: #dfdfdf;
    }
  }
  .theme-block__item__image {
    display: block;
    margin-bottom: 15px;

    img {
      display: block;
    }
  }
  .theme-block__item__title {
    margin-bottom: 15px;

    font-family: @font-main;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.11;
    letter-spacing: -0.4px;
    color: #2b3033;
  }
  .theme-block__item__text {
    font-size: 15px;
    line-height: 1.2;
    color: #3d4348;
  }
}

.theme-block--opinions {
  .theme-block__item {
    margin-bottom: 20px;

    overflow: hidden;
    &:first-of-type {
      margin-top: 30px;
    }
    &:last-of-type {
      margin-bottom: 30px;
    }
  }

  .theme-block__item__image {
    float: left;
    margin-right: 15px;
  }

  .theme-block__item__author {
    margin-bottom: 10px;

    font-family: @font-main;
    font-size: 18px;
    font-weight: 500;
    letter-spacing: -0.4px;
    color: #2b3033;
  }

  .theme-block__item__title {
    font-size: 15px;
    color: #3d4348;
  }
}

.theme-block--videos {
  .theme-block__item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    overflow: hidden;
    &:first-of-type {
      margin-top: 30px;
    }
    &:last-of-type {
      margin-bottom: 30px;
    }
  }

  .theme-block__item__image {
    margin-right: 10px;

    img {
      display: block;
    }
  }

  .theme-block__item__title {
    font-size: 14px;
    line-height: 1.07;
    color: #3d4348;
  }
}

.theme-block--quiz {
  padding-bottom: 25px;
  .theme-block__item__title {
    .margin-y(30px, 25px);

    font-family: @font-main;
    font-size: 17px;
    font-weight: 600;
    letter-spacing: -0.3px;
    color: #2b3033;
  }
}

.play-btn {
  position: relative;

  &::before {
    .css-triangle-right(#fff, 9px, 7px);
    position: absolute;
    top: calc(~'50% - 7px');
    left: calc(~'50% - 4.5px');
  }

  &::after {
    content: '';
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;

    border-radius: 50%;
    border: solid 2px #ffffff;
  }
}

.play-btn--big {
  &::before {
    border-width: 8px 13px;
    top: calc(~'50% - 7.5px');
    left: calc(~'50% - 4px');
  }

  &::after {
    width: 60px;
    height: 60px;
  }
}

/*
 * buttons
 */

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 77px;
  height: 28px;

  font-size: 14px;
  color: #2b3033;

  border: 1px solid @active-color;
  background-color: transparent;
}

/*
 * form
 * start
 */

label {
  cursor: pointer;
}

textarea {
  width: 100%;
  height: 70px;
  padding: 20px;

  border: #bbbbbb solid 1px;
  resize: vertical;
}

.input {
  width: 100%;
  padding: 15px;

  background-color: #fff;
  border: 1px solid #e1e0e0;

  &:invalid {
    border-color: #fa6565;
  }
}

.jq-checkbox {
  width: 16px;
  height: 16px;
  margin-right: 10px;

  border: 1px solid #e1e0e0;
  background-color: #fff;
  cursor: pointer;

  &.checked .jq-checkbox__div {
    width: 14px;
    height: 14px;
    border-radius: 2px;
    background-image: url(../img/checkbox.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.jq-radio {
  width: 14px;
  height: 14px;
  margin-right: 10px;

  border: 1px solid #cfcfcf;
  background-color: #ffffff;

  border-radius: 50%;
  cursor: pointer;

  &.checked .jq-radio__div {
    width: 6px;
    height: 6px;
    margin-top: 3px;
    margin-left: 3px;

    border-radius: 50%;
    background-color: @active-color;
  }
}

.quiz-form {
  label {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    cursor: pointer;
  }
  .jq-radio {
    flex-shrink: 0;
  }
}

.quiz-btn {
  display: block;
  width: 190px;
  .margin-x(auto);

  color: #2b3033;
  font-size: 15px;
  font-weight: 600;
  line-height: 22px;

  border: 1px solid @active-color;
  background-color: #fff;
}

.quiz-result__item {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 15px;
}

.quiz-result__item--small {
  display: block;
}

.quiz-result__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quiz-result__item__left {
  width: 85%;

  color: #2b3033;
}

.quiz-result__item__right {
  flex-shrink: 0;

  color: #475056;
  font-size: 13px;
}

.quiz-result__progress {
  position: relative;
  margin-top: 10px;
}

.quiz-result__progress-all {
  display: block;
  width: 100%;
  height: 10px;

  border-radius: 2px;
  background-color: #cacaca;
}

.quiz-result__progress-current {
  position: absolute;
  top: 0;
  left: 0;

  display: block;
  height: 10px;

  background-color: #00a6ff;
  border-radius: 2px;
}

/**
 * form
 * end
 */

/**
 * ads
 * start
 */

.that-full-width {
  display: block;
  max-width: 1120px;
  .margin-x(auto);
  margin-bottom: 20px;

  img {
    display: block;
  }
}

/**
 * ads
 * end
 */

/**
 * share
 * start
 */

.sharing {
  margin-bottom: 45px;

  p {
    margin-bottom: 15px;

    color: #475056;
    font-size: 15px;
    font-weight: 600;
  }
}

.share-btn {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin-right: 10px;

}

.share-btn--fb {
  background-image: url(../img/soc-fb.svg);
}

.share-btn--tw {
  background-image: url(../img/soc-tw.svg);
}

.share-btn--vk {
  background-image: url(../img/soc-vk.svg);
}

/**
 * share
 * end
 */
