.news-item {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 10px 10px 0 10px;

  border: 1px solid #dfdfdf;
  border-bottom: 2px solid @active-color;
  background-color: #fff;
}

.news-item--hero {
  .news-item__image {
    margin-bottom: 18px;
  }
  .news-item__title {
    margin-bottom: 17px;

    font-size: 22px;
    line-height: 1.09;
  }
  .news-item__footer {
    .padding-y(15px);
  }
}

.news-item__image {
  display: block;
  margin: -11px -11px 15px;

  img {
    display: block;
    width: 100%;
    height: 100%;
  }
}

.news-item__date {
  display: block;
  margin-bottom: 10px;

  font-size: 14px;
  letter-spacing: 0.3px;
  color: #6d6d6d;
}

.news-item__title {
  margin-bottom: 15px;

  font-family: @font-main;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.11;
  letter-spacing: -0.4px;
  color: #2b3033;
}

.news-item__short-text {
  margin-bottom: 15px;

  line-height: 1.19;
  color: #3d4348;
}

.news-item__footer {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  .padding-y(10px);

  color: #6d6d6d;
  font-size: 13px;

  &::before {
    content: '';
    position: absolute;
    top: 0;

    display: block;
    width: 100%;
    height: 1px;
    background-color: #dfdfdf;
  }
}

.news-item__tags {
  letter-spacing: 0.3px;

  a + a {
    margin-left: 5px;
  }
}

.news-item__comments {
  position: relative;

  &::before {
    content: url('../img/comments-icon.svg');
    position: absolute;
    top: 0;
    bottom: 0;
    right: calc(~'100% + 5px');

    display: block;
    width: image-width('../img/comments-icon.svg');
    height: image-height('../img/comments-icon.svg');
    margin: auto;
  }
}

.news-item__author {
  position: absolute;
  top: -1px;
  left: -1px;
  width: calc(~'100% + 2px');
  padding: 5px 10px;

  color: #fff;
  font-size: 14px;
  letter-spacing: .3px;
  background-color: rgba(82, 78, 78, .82);
}
